.button-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
}

.button-style {
  background: transparent;
  border: 1px solid #ccc;
  padding: 5px 15px;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  font-size: 12px;
}

.divider {
  height: 0.25px;
  background-color: #ccc;
  width: 100%;
  margin: 0 0;
}

.image-style {
  width: 150px;
  height: auto;
}

.card-style {
  width: 100%;
  height: 100%;
}

.subtitle-style {
  margin-top: 0;
  font-weight: 30;
  font-size: 12px;
}

.titleStyle {
  margin-bottom: 0;
  font-size: 18px;
  font-family: 'Helvetica';
}
